import { Button, Form, Input } from 'antd';
import React from 'react';

export const LoginPage = () => {
    return (
        <Form>
            <Form.Item
                name="login"
                rules={[
                    {
                        required: true,
                        message: ' !'
                    }
                ]}
            >
                <Input placeholder="" />
            </Form.Item>

            <Form.Item
                name="password"
                rules={[
                    {
                        required: true,
                        message: ' !'
                    }
                ]}
            >
                <Input.Password placeholder="" />
            </Form.Item>

            <Form.Item>
                <Button type="primary" htmlType="submit">
                    
                </Button>
            </Form.Item>
        </Form>
    );
};
