import { Card } from 'antd';
import React from 'react';

const testData = [
    { no: 1, title: '' },
    { no: 2, title: '' },
    { no: 3, title: '' },
    { no: 4, title: '' },
    { no: 5, title: '' },
    { no: 6, title: '' },
    { no: 7, title: '' }
];

const listItems = testData.map((item) => (
    <li>
        {item.no}. {item.title}
    </li>
));

export const WeekPage = () => {
    return (
        <>
            <Card title="">{listItems}</Card>
            <Card title="">{listItems}</Card>
            <Card title="">{listItems}</Card>
            <Card title="">{listItems}</Card>
            <Card title="">{listItems}</Card>
            <Card title="">{listItems}</Card>
        </>
    );
};
